import React from 'react';
import PropTypes from 'prop-types';
import './App.css';

class TextInput extends React.Component {
	constructor(props) {
		super(props);
		this.state = {value: ''};
		this.inputRef = React.createRef();
	}

	componentDidMount() {
		this.inputRef.current.focus();
	}

	tookInput = (e) => {
		this.setState((state) => {
			return {value: e.target.value};
		}).then(() => {
			console.log(this.state.value);
		});
	}

	render() {
		return (
			<label className="text-label">
				{this.props.target}
				<input id='input' type="text" ref={this.inputRef} onChange={this.tookInput} className="text-input"/>
			</label>
		);
	}
}

TextInput.propTypes = {
	target: PropTypes.string,
	callback: PropTypes.func
};

class App extends React.Component {
	constructor(props) {
		super(props);
	}

	reloadTarget = () => {
		console.log("Reloaded");
	}

	render() {
		return (
			<TextInput callback={this.reloadTarget} target="test"/>
		);
	}
}

export default App;
